
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import LSwiper1 from "../components/LSwiper1";
import LSwiper3 from "../components/LSwiper3";

export default {
components: {
LSwiper3,
LSwiper1,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Panel,
Swiper,
SwiperItem,
Group,
Cell,
Grid,
GridItem,
XImg,
GroupTitle,
Box,
Tab,
TabItem,
Confirm,
},
data() {
return {
pid: sessionStorage.getItem("pid"),
activityList254: {},
activityList255: {},
activityList256: {},
activityList262: {},
activityList257: {},
activityList258: {},
activityList259: {},
activityList302: {},
activityList306: {},
activityList307: {},
activityList308: {},
activityList309: {},
getDataUrl: "/api/gdekhback/phone/activity_list",
};
},
mounted: function () {
this.getBannerList();
},
methods: {
showTips(message) {
this.$vux.toast.show({
text: message,
type: "warn",
width: "10em",
});
},
getBannerList() {
this.$http
.get("/api/gdekhback/Redpacket/gdicardView", {
params: {
pid: this.pid,
},
})
.then((res) => {
if (res.data.status === 1) {
this.activityList254 = res.data.data[254] || {};
this.activityList255 = res.data.data[255] || {};
this.activityList256 = res.data.data[256] || {};
this.activityList262 = res.data.data[262] || {};
this.activityList257 = res.data.data[257] || {};
this.activityList258 = res.data.data[258] || {};
this.activityList259 = res.data.data[259] || {};
this.activityList302 = res.data.data[302] || {};
this.activityList306 = res.data.data[306] || {};
this.activityList307 = res.data.data[307] || {};
this.activityList308 = res.data.data[308] || {};
this.activityList309 = res.data.data[309] || {};
}
});
},
go(item) {
let url = item.url;
if (url != null && url !== "") {
if (/^(http:|https:)?\/\//g.test(url)) {

window.location.href = url;
} else if (/^\//g.test(url)) {

this.$router.push(url);
} else if (/^s:\/\//g.test(url)) {

let message = url.replace(/^s:\/\//g, "");
this.showTips(message);
}
} else {
console.log(item);
}
},
},
};



