import "swiper/dist/css/swiper.min.css"
import Swiper from 'swiper';

export default {
    name: "LSwiper3",
    components: {},
    props: {
        list: {
            type: Array,
            default: []
        }
    },
    data() {
        return {};
    },
    mounted() {
        let _this = this;
        new Swiper(this.$refs.swiper, {
            loop: true,
            loopedSlides: 5,
            slidesPerView: 'auto',
            // autoplay: {
            //     delay: 3000,
            //     stopOnLastSlide: false,
            //     disableOnInteraction: false,
            // },
            centeredSlides: true,
            // initialSlide: 2,
            watchSlidesProgress: true,
            pagination: {
                el: this.$refs["swiper-pagination"],
            },
            on: {
                click() {
                    let realIndex = this.realIndex;
                    _this.handleClick(_this.list[realIndex]);
                },
                progress() {
                    for (let i = 0; i < this.slides.length; i++) {
                        let slide = this.slides.eq(i);
                        let slideProgress = this.slides[i].progress;
                        let modify = 0;
                        if (Math.abs(slideProgress) > 0) {
                            modify = (Math.abs(slideProgress) - 1) * 0.4 + 1;
                        }
                        let number = slide[0].offsetWidth * 0.8;// ()
                        let translate = slideProgress * modify * number + 'px';
                        let scale = 1 - Math.abs(slideProgress) / 5; // ()
                        let zIndex = 999 - Math.abs(Math.round(10 * slideProgress));
                        slide.transform('translateX(' + translate + ') scale(' + scale + ')');
                        slide.css('zIndex', zIndex);
                        slide.css('opacity', 1);
                        if (Math.abs(slideProgress) > 1) { // 
                            slide.css('opacity', 0);
                        }
                    }
                },
                setTransition: function (transition) {
                    for (let i = 0; i < this.slides.length; i++) {
                        let slide = this.slides.eq(i);
                        slide.transition(transition);
                    }
                }
            },
        })
    },
    methods: {
        handleClick(item) {
            this.$emit("item-click", item);
        }
    }
}



