import "swiper/dist/css/swiper.min.css"
import Swiper from 'swiper';

export default {
    name: "LSwiper1",
    components: {},
    props: {
        list: {
            type: Array,
            default: []
        }
    },
    data() {
        return {};
    },
    computed: {},
    mounted() {
        new Swiper(this.$refs.swiper, {
            slidesPerView: 4,
        })
    },
    methods: {
        handleClick(item) {
            this.$emit("item-click", item);
        }
    }
}



